<template>
	<view class="content">
		<view class="sticky">
			<!-- 状态栏 -->
			<view :style="'height:' + StatusbarHeight + 'px;'"><slot name="statusbar"></slot></view>
			<!-- Header -->
			<slot name="header">header</slot>
		</view>

		<slot><view style="flex: 1;justify-content: center;">flex: 1;</view></slot>

		<view class="sticky"><slot name="footer">footer</slot></view>
	</view>
</template>

<script>
export default {
	data() {
		return {};
	},
	props: {},
	methods: {},
	computed: {
		//状态栏高度
		StatusbarHeight() {
			return uni.getSystemInfoSync().statusBarHeight;
		}
	}
};
</script>

<style lang="scss" scoped>
@import '../common/public.scss';
.content {
	//#ifndef APP-PLUS
	min-height: 100vh;
